body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #C3DCEF;
  padding: 0;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;

}

.container{
  background: #E47673;
  width: 100%;
  height: 100%;
  position: relative;
  border-radius: 12px;
}

.add-task{
  display: flex;
  justify-content: space-between;
  margin: 10px;
  position: relative;
  top: 10px;
  padding: 10px;
  background: #F7CAC7;
  height: 30px;
  color: #E47673;
  border-radius: 10px;
}

.task-input input{
  border: none;
  outline: none;
  height: 30px;
  color: #E47673;
  background: #F7CAC7;
  font-size: 20px;
  font-weight: bold;
}

.add-control{
  color: #E47673;
  font-size: 20px;
  font-weight: bolder;
  cursor: pointer;
}


/* Items listing */
.items{
  display: flex;
  justify-content: space-between;
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 20px;
  border-bottom: 2px solid white;
  margin-left: 10px;
  margin-right: 10px;
}

.item-names{
  color: white;
}

.item-names input{
  height: 20px;
  width: 20px;
  position: relative;
  top: 2px;
}

.quantity{
  background: #F7CAC7;
  color: #E47673;
  width: 75px;
  text-align: center;
  border-radius: 12px;
}

.left{
  position: relative;
  right: 10px;
  cursor: pointer;
}

.right{
  position: relative;
  left: 10px;
  cursor: pointer;
}

/* total component */
.total{
    color: white;
    font-size: 20px;
    text-align: right;
    padding: 10px;
}